import { Injectable } from '@angular/core';
import {Http} from '@angular/http';
import {endpoints} from './endpoints';

@Injectable()
export class DomainService {
  
  private STRATEGIC_AUDIENCE:string[];
  private BUSINESS_OBJECTIVES:string[];
  private KPIS:string[];
  private PRODUCTS:string[];
  private CLIENTS:string[];

 public getKPIS() {
   return this.KPIS;
 }
 
 public getProducts() {
   return this.PRODUCTS;
 }
 
 public getClients() {
   return this.CLIENTS;
 }
 
  constructor(private _http:Http) {
    this._http.get(endpoints.GET_DOMAIN_DATA+"?name=Strategic Audience")
              .map(res => res.json())
              .subscribe(
                data =>this.STRATEGIC_AUDIENCE = data.names
              );
    this._http.get(endpoints.GET_DOMAIN_DATA+"?name=Business Objective")
              .map(res => res.json())
              .subscribe(
                data =>this.BUSINESS_OBJECTIVES = data.names
              );
    this._http.get(endpoints.GET_DOMAIN_DATA+"?name=KPI")
              .map(res => res.json())
              .subscribe(
                data =>this.KPIS = data.names
              );
    this._http.get(endpoints.GET_DOMAIN_DATA+"?name=Product Name")
              .map(res => res.json())
              .subscribe(
                data => this.PRODUCTS = data.names
              );
    this._http.get(endpoints.GET_DOMAIN_DATA+"?name=Client Name")
              .map(res => res.json())
              .subscribe(
                data =>this.CLIENTS = data.names
              );
  }
  
  public getStrategicAudience() {
    return this.STRATEGIC_AUDIENCE;
  }
  
  public getBusinessObjectives() {
    return this.BUSINESS_OBJECTIVES;
  }
  

}
